#app {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#app-bar {
  display: inline-block;
  padding: 0 8px;

  z-index: 1300;
  background-color: #FFF;
}

#function-plot {
  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background: #00F;
  overflow: hidden;
}

#function-plot > canvas {
  width: 100%;
  height: 100%;
}

#function-input {
  margin-left: 3px;
  -ms-flex-positive: 1;
      flex-grow: 1
}

#help-button {
  margin-left: 10px;
}

#help-text {
  padding: 30px;
  width: 500px;
}

#help-text > p {
  font-size: 14px;
  line-height: 2;
  text-align: justify;
  text-justify: inter-word;
}

div.drawer-content {
  overflow-x: hidden;
}

div.variable-slider {
  width: 90%;
}



